export default {
  name: 'app-header',
  methods: {
    onGoBack () {
      this.$router.go(-1)
    },
    async onLogout () {
      try {
        await userService.logout()
        this.$router.push({ name: 'home' })
        this.$store.commit('setDefaultUser')
      } catch (exception) {
        console.log(exception.message)
      }
    }
